<template>
	<view>
		<view class="cu-chat">
			<view class="padding flex flex-direction">
				<view class="cu-form-group">
					<view class="title">姓名</view>
					<input v-model="post_huzu" placeholder="户主名称"></input>
				</view>
				<view class="cu-form-group">
					<view class="title">身份证号</view>
					<input v-model="post_shenfen" placeholder="户主身份证号"></input>
				</view>
				<view class="cu-form-group">
					<view class="title">手机号码</view>
					<input disabled="true" v-model="post_shouji" placeholder="输入手机号码"></input>
					<view class="cu-capsule radius">
						<button open-type="getPhoneNumber" @getphonenumber="huoqu_shouji" class="cu-tag line-blue">点击获取手机号</button>
					</view>
				</view>
				<button @click="tijiao_xinxi()" class="cu-btn bg-blue lg" style="margin-top: 2vw;">立即绑定</button>
			</view>
		</view>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				post_huzu: "",
				post_shenfen: "",
				post_shouji: "",

			}
		},
		methods: {
			tijiao_xinxi: function() {
				let _this = this

				if (!_this.post_huzu) {
					uni.showModal({
						title: '提示',
						content: "姓名不能为空",
						showCancel: false,
						success: function() {}
					});
					return false;
				}


				if (!(/^[0-9,x,X]{18}$/.test(_this.post_shenfen))) {
					uni.showModal({
						title: '提示',
						content: "身份证号码有误，请重填",
						showCancel: false,
						success: function() {}
					});
					return false;
				}

				if (!(/^[0-9]{11}$/.test(_this.post_shouji))) {
					uni.showModal({
						title: '提示',
						content: "手机号有误，请重填",
						showCancel: false,
						success: function() {}
					});
					return false;
				}


				let data = {
					ownerName: _this.post_huzu,
					idCard: _this.post_shenfen,
					mobilePhone: _this.post_shouji,
					quartersId: getApp().globalData.public_user_property_id,
				}
				_this.wangluo_qingqiu('/wx/ipms/verificationowner', data, true, true, 'POST').then(value => {
					if (value) {
						getApp().globalData.public_user_property = true
						uni.showModal({
							title: '提示',
							content: '系统已有房产信息，是否前往查看',
							success: function(res) {
								if (res.confirm) {
									uni.navigateTo({
										url:"../wode_zican/wode_zican"
									})
									console.log('用户点击确定');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						});
					} else {
						uni.showModal({
							title: '提示',
							content: "暂无房产信息，请联系物业录入。",
							showCancel: false,
							success: function() {}
						});
					}
				})


			},
			huoqu_xinxi: function() {

				let _this = this

				let data = {
					quartersId: getApp().globalData.public_user_property_id,
				}
				_this.wangluo_qingqiu('/wx/ipms/getcustomerdata', data, true, true, 'GET').then(value => {
					if (value) {
						_this.post_huzu = value.customerName
						_this.post_shenfen = value.idCard
						_this.post_shouji = value.mobilePhone
					}
				})
			},
			huoqu_shouji: function(res) {
				let _this = this
				let encryptedData = res.detail.encryptedData
				if (!_this.isEmpty(encryptedData)) {
					let iv = res.detail.iv
					let data = {
						encryptedData: encryptedData,
						iv: iv,
					}
					_this.wangluo_qingqiu('/wx/user/phone', data, true, true, 'POST').then(value => {

						if (value) {
							_this.wangluo_qingqiu('/wx/ipms/getphoneno', null, true, true, 'GET').then(value => {

								if (value) {
									_this.post_shouji = value.mobilePhone
								}
							})
						}

					})
				} else {}

			},
			huoqu_shoujihao: function() {

			}


		},
		onLoad: function() {
			this.huoqu_xinxi()
		}
	}
</script>

